import React, { Fragment } from 'react';
import { Paper } from '@material-ui/core'
import API from '../../../../Utils/API/API'
import { Helmet } from "react-helmet";
import Nav from '../../../UI/Nav';


class CampaignEdit extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            campaign: null,
            tags: [],
            templates: []
        }
    }

    useQuery = () => {
        return new URLSearchParams(this.props.location.search);
    }

    componentDidMount() {

        if (this.useQuery().get('id')) {
            API.get(`http://localhost:5000/api/v1/campaigns?id=${this.useQuery().get('id')}`)
                .then(res => this.setState({ campaign: res }))
                .catch(err => console.log(err));
        }

        API.get('http://localhost:5000/api/v1/templates')
            .then(res => this.setState({ templates: res }))
            .catch(err => console.log(err))

        API.get('http://localhost:5000/api/v1/tags')
            .then(res => this.setState({ tags: res }))
            .catch(err => console.log(err))
    }

    render() {
        return (
            <>
                <Helmet>
                    <title>EDIT CAMPAIGN | S:YNTH</title>
                </Helmet>
                <Nav />
                <div className="CreateCampaign">
                    <Paper elevation={3} style={{ margin: '10px 50px' }}>
                        campaign: {JSON.stringify(this.state.campaign)}
                        template to send  {JSON.stringify(this.state.templates)}
                        audience tags to send to {JSON.stringify(this.state.tags)}
                    </Paper>
                </div>
            </>
        )
    }

}

export default CampaignEdit;